@mixin telefono {
    @media (min-width:$telefono){
        @content;
    }
}
@mixin tablet {
    @media (min-width:$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width:$desktop) {
        @content;
    }
}

@mixin DGrid($columnas, $espaciado ) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}